// ContentPanel.jsx
import React from 'react';

const ContentPanel = ({ node }) => {
  if (!node) return (
    <div className="bg-gray-100 rounded-lg p-6 text-gray-500 font-serif shadow-sm border border-gray-200 h-[50vh] flex items-center justify-center">
      <p>Select a node to view its content</p>
    </div>
  );
  
  // Extract numbered points from content
  const extractPoints = (content) => {
    if (!content) return [];
    const matches = content.match(/\{([^}]+)\}/g) || [];
    return matches.map(match => match.slice(1, -1).trim());
  };

  const points = extractPoints(node.content);
  
  // Get background color based on node type
  const getNodeTypeStyles = () => {
    switch (node.node_type) {
      case 'reason':
        return 'bg-blue-50 text-blue-600';
      case 'thesis':
        return 'bg-green-50 text-green-600';
      case 'antithesis':
        return 'bg-red-50 text-red-600';
      case 'synthesis':
        return 'bg-indigo-50 text-indigo-600';
      case 'question':
        return 'bg-gray-100 text-gray-600';
      default:
        return 'bg-gray-100 text-gray-600';
    }
  };

  return (
    <div className="bg-white rounded-lg p-6 text-gray-800 font-serif shadow-sm border border-gray-200 h-[50vh] flex flex-col">
      <h3 className="text-xl font-semibold mb-2 pb-2 border-b border-gray-200 text-gray-900 leading-snug flex-shrink-0">
        {node.summary}
      </h3>
      <div className="overflow-auto flex-grow">
        <div className="space-y-3 py-2">
          {points.length > 0 ? (
            points.map((point, index) => (
              <div key={index} className="flex gap-3">
                <span className="text-gray-400 font-semibold min-w-[1.25rem]">
                  {index + 1}.
                </span>
                <p className="text-gray-700 whitespace-pre-wrap leading-relaxed">
                  {point}
                </p>
              </div>
            ))
          ) : (
            <p className="text-gray-700 whitespace-pre-wrap leading-relaxed">
              {node.content || "No content available for this node."}
            </p>
          )}
        </div>
      </div>
      <div className="mt-3 flex justify-between items-center border-t border-gray-100 pt-2 flex-shrink-0">
        <span className={`text-xs px-2 py-1 rounded-full ${getNodeTypeStyles()}`}>
          {node.node_type}
        </span>
        {node.nonsense && (
          <span className="text-xs px-2 py-1 bg-red-50 text-red-600 rounded-full">
            Nonsense
          </span>
        )}
        {node.identical_to && (
          <span className="text-xs px-2 py-1 bg-blue-50 text-blue-600 rounded-full">
            Identical to another node
          </span>
        )}
      </div>
    </div>
  );
};

export default ContentPanel;