// AncestryPanel.jsx
import React, { useEffect, useRef } from 'react';

const AncestryPanel = ({ node, graphData }) => {
  const scrollContainerRef = useRef(null);
  
  // Scroll to the current node when it changes
  useEffect(() => {
    if (scrollContainerRef.current && node) {
      // Add a small delay to ensure the DOM has updated
      setTimeout(() => {
        const highlightedElement = scrollContainerRef.current.querySelector('.highlighted-node');
        if (highlightedElement) {
          highlightedElement.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }
      }, 100);
    }
  }, [node]);

  if (!node) return (
    <div className="bg-gray-100 rounded-lg p-6 text-gray-500 font-serif shadow-sm border border-gray-200 h-[50vh] flex items-center justify-center">
      <p>Select a node to view its ancestry</p>
    </div>
  );
  
  // Find the root question node
  const findRootQuestion = () => {
    const questionNode = Object.values(graphData).find(n => n.node_type === 'question');
    return questionNode || null;
  };
  
  const getAncestry = (currentNode) => {
    const ancestry = [];
    const rootQuestion = findRootQuestion();
    
    // If the current node is a reason
    if (currentNode.node_type === 'reason') {
      // 1. Add the current reason node first
      ancestry.push(currentNode);
      
      // 2. Add the central question second
      if (rootQuestion) {
        ancestry.push(rootQuestion);
      }
      
      // 3. Add the parent thesis third
      if (currentNode.parent_id) {
        const parentThesis = graphData[currentNode.parent_id];
        if (parentThesis) {
          ancestry.push(parentThesis);
          
          // 4. Continue adding any other ancestors
          let parentId = parentThesis.parent_id;
          // Skip the root question since we already added it
          if (parentId === rootQuestion?.id) {
            parentId = null;
          }
          
          while (parentId) {
            const ancestor = graphData[parentId];
            if (ancestor && ancestor.id !== rootQuestion?.id) { // Avoid duplicate question
              ancestry.push(ancestor);
              parentId = ancestor.parent_id;
              // Skip the root question if we encounter it again
              if (parentId === rootQuestion?.id) {
                parentId = null;
              }
            } else {
              break;
            }
          }
        }
      }
    } else {
      // For non-reason nodes, create a normal ancestry chain
      // Start with ancestors
      const ancestorChain = [];
      let currentId = currentNode.id;
      let currentAncestor = currentNode;
      
      // First collect the chain from current node to root
      while (currentAncestor) {
        ancestorChain.unshift(currentAncestor);
        if (currentAncestor.parent_id) {
          currentAncestor = graphData[currentAncestor.parent_id];
        } else {
          break;
        }
      }
      
      // Add the ancestor chain to our result
      ancestry.push(...ancestorChain);
      
      // If current node isn't a reason, find its reasons and add them at the end
      if (currentNode.node_type !== 'reason') {
        const reasons = Object.values(graphData)
          .filter(n => n.parent_id === currentNode.id && n.node_type === 'reason');
        
        if (reasons.length > 0) {
          ancestry.push(...reasons);
        }
      }
    }
    
    return ancestry;
  };
  
  const ancestry = getAncestry(node);
  
  // Get appropriate label for each node in the chain
  const getLevelLabel = (ancestor, index) => {
    if (ancestor.node_type === 'reason') {
      return 'Reason';
    } else if (ancestor.node_type === 'question') {
      return 'Central Question';
    } else if (ancestor.node_type === 'thesis') {
      return 'Thesis';
    } else if (ancestor.node_type === 'antithesis') {
      return 'Antithesis';
    } else if (ancestor.node_type === 'synthesis') {
      return 'Synthesis';
    } else {
      return `Level ${index}`;
    }
  };
  
  return (
    <div className="bg-white rounded-lg p-4 text-gray-800 font-serif shadow-sm border border-gray-200 h-[50vh] flex flex-col">
      <h3 className="text-lg font-semibold mb-2 pb-2 border-b border-gray-200 text-gray-900 leading-snug flex-shrink-0">
        Dialectical Chain
      </h3>
      <div 
        ref={scrollContainerRef}
        className="overflow-auto flex-grow pr-2"
      >
        {ancestry.map((ancestor, index) => {
          // Determine if this is the highlighted node
          const isHighlighted = ancestor.id === node.id;
          
          return (
            <div 
              key={index} 
              className={`relative mb-3 pl-4 ${isHighlighted ? 'highlighted-node' : ''}`}
              style={{
                borderLeft: `2px solid ${
                  isHighlighted
                    ? '#db2777' // Pink for selected node
                    : ancestor.node_type === 'reason' 
                      ? '#3b82f6' // Blue for reasons
                      : '#d1d5db' // Light gray for others
                }`,
                backgroundColor: isHighlighted ? '#fdf2f8' : 'transparent',
                padding: '0.5rem 0.5rem 0.5rem 1rem',
                borderRadius: '0 0.25rem 0.25rem 0'
              }}
            >
              <div className="text-xs text-gray-500 mb-1 italic">
                {getLevelLabel(ancestor, index)}
              </div>
              <div className={`text-gray-900 text-sm leading-snug ${isHighlighted ? 'font-medium' : 'font-normal'}`}>
                {ancestor.summary}
              </div>
              <div className={`text-xs mt-1 inline-block px-2 py-0.5 rounded-full ${
                ancestor.node_type === 'reason' ? 'bg-blue-50 text-blue-600' : 
                ancestor.node_type === 'question' ? 'bg-gray-100 text-gray-600' : 
                ancestor.node_type === 'thesis' ? 'bg-green-50 text-green-600' :
                ancestor.node_type === 'antithesis' ? 'bg-red-50 text-red-600' : 
                ancestor.node_type === 'synthesis' ? 'bg-indigo-50 text-indigo-600' : 'bg-gray-100 text-gray-600'
              }`}>
                {ancestor.node_type}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default AncestryPanel;