// src/App.js
import React, { useState, useEffect } from 'react';
import CanvasGraph from './components/Graph/CanvasGraph';

function App() {
  const [graphData, setGraphData] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  // Load graph data from the public folder
  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      try {
        // Fetch the graph data from the public folder
        const response = await fetch(`${process.env.PUBLIC_URL}/graphs/graph.json`);
        
        if (!response.ok) {
          throw new Error(`Failed to load graph data: ${response.status} ${response.statusText}`);
        }
        
        const data = await response.json();
        console.log('Loaded graph data. Nodes:', Object.keys(data).length);
        
        setGraphData(data);
        setIsLoading(false);
      } catch (error) {
        console.error('Error loading graph data:', error);
        setError(`Failed to load graph data: ${error.message}`);
        setIsLoading(false);
      }
    };

    fetchData();
  }, []);

  // Show loading state
  if (isLoading) {
    return (
      <div className="h-screen w-full flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-800 mx-auto"></div>
          <p className="mt-4 text-gray-700">Loading graph data...</p>
        </div>
      </div>
    );
  }

  // Show error state
  if (error) {
    return (
      <div className="h-screen w-full flex items-center justify-center bg-gray-50">
        <div className="text-center text-red-600">
          <h2 className="text-2xl font-bold">Error</h2>
          <p className="mt-2">{error}</p>
          <button 
            className="mt-4 px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
            onClick={() => window.location.reload()}
          >
            Retry
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="App">
      {/* Render the Canvas-based graph */}
      <CanvasGraph data={graphData} />
    </div>
  );
}

export default App;